import React from 'react';
import { Container, Typography, Box } from '@mui/material';

const About = () => {
  return (
    <Container maxWidth="md" sx={{backgroundColor: 'white', padding: 5, marginY: 5, borderRadius: '8px' }}>
      {/* Ana Balk */}
      <Box my={4}>
        <Typography variant="h4" component="h1" gutterBottom align="center">
          Hakkmda
        </Typography>
      </Box>

      {/* Eitim ve Deneyim */}
      <Box my={4}>
        <Typography variant="h5" component="h2" gutterBottom>
          Eitim ve Deneyim
        </Typography>
        <Typography variant="body1" paragraph>
          Mersin niversitesi Bilgisayar Mhendislii blmnden mezun oldum ve bu alandaki bilgi birikimimi yazlm gelitirme sektrnde profesyonel olarak kullanmaktaym. 
          <strong> React</strong> ve <strong>React Native</strong> gibi modern teknolojiler zerinde uzmanlatm ve hem front-end hem de back-end gelitirme srelerinde geni bir deneyime sahibim.
        </Typography>
      </Box>

      {/* Front-End Gelitirme */}
      <Box my={4}>
        <Typography variant="h5" component="h2" gutterBottom>
          Front-End Gelitirme
        </Typography>
        <Typography variant="body1" paragraph>
          Front-end gelitirme srelerinde <strong>HTML</strong>, <strong>CSS</strong> ve <strong>JavaScript</strong> gibi temel teknolojileri kullanarak, estetik ve kullanc dostu arayzler gelitiriyorum. 
          <strong> Tailwind CSS</strong> gibi stil ktphaneleriyle alma deneyimim sayesinde, grsel adan zengin ve optimize edilmi web siteleri oluturmaktaym. 
          Ayrca, <strong>responsive</strong> tasarmlar sayesinde projelerin her cihazda mkemmel almasn salyorum.
        </Typography>
      </Box>

      {/* Mobil Uygulama Gelitirme */}
      <Box my={4}>
        <Typography variant="h5" component="h2" gutterBottom>
          Mobil Uygulama Gelitirme
        </Typography>
        <Typography variant="body1" paragraph>
          <strong>React Native</strong> kullanarak, iOS ve Android platformlar iin performans odakl mobil uygulamalar gelitiriyorum. 
          Kullanclarn ihtiyalarna en uygun zmleri sunmak iin <strong>API</strong> entegrasyonlar ve veri ynetimi gibi konularda derinlemesine bilgiye sahibim. 
          Hedefim, her iki platformda da kullanclarn beklentilerini aan uygulamalar retmektir.
        </Typography>
      </Box>

      {/* Srekli renme ve Geliim */}
      <Box my={4}>
        <Typography variant="h5" component="h2" gutterBottom>
          Srekli renme ve Geliim
        </Typography>
        <Typography variant="body1" paragraph>
          Yazlm dnyas srekli gelien bir alan olduu iin, en yeni teknolojileri ve en iyi uygulamalar yakndan takip ederek kendimi srekli gelitiriyorum. 
          Projelerde, takm arkadalarmla etkili bir i birlii yaparak, kaliteli ve zamannda teslim edilen zmler retmeye zen gsteriyorum.
        </Typography>
      </Box>

      {/* UX/UI Tasarm */}
      <Box my={4}>
        <Typography variant="h5" component="h2" gutterBottom>
          UX/UI Tasarm
        </Typography>
        <Typography variant="body1" paragraph>
          Yazlm gelitirmenin yan sra, <strong>kullanc deneyimi (UX)</strong> ve <strong>kullanc arayz (UI)</strong> tasarmna da zel bir ilgi duyuyorum. 
          Bu alandaki bilgi birikimimi projelerime entegre ederek, ilevsel olduu kadar grsel adan da tatmin edici deneyimler sunmay hedefliyorum.
        </Typography>
      </Box>

      {/* Sonu */}
      <Box my={4}>
        <Typography variant="h5" component="h2" gutterBottom>
          Sonu
        </Typography>
        <Typography variant="body1" paragraph>
          Bilgisayar mhendislii eitimim ve yazlm gelitirme konusundaki kapsaml tecrbemle, teknolojiyi kullanarak yaratc, yeniliki ve srdrlebilir zmler retmeye devam ediyorum. 
          Hedefim, her zaman kullanclar iin deer yaratan projeler gelitirmek.
        </Typography>
      </Box>
    </Container>
  );
};

export default About;



