import React from 'react';
import { Container, Typography, Stack, Box, Button } from '@mui/material';
import { useNavigate } from 'react-router-dom';

const Home = () => {
    const navigate = useNavigate();

    const handleNavigation = (path) => {
        navigate(path);
    };

    return (
        <Container maxWidth="sm" style={{ textAlign: 'center', marginTop: '50px', marginBottom: '50px'}}>
            <Box
                sx={{
                    bgcolor: '#f5f5f5', // Arka plan rengi
                    borderRadius: '8px', // Ke yuvarlama
                    boxShadow: 3, // Glge efekti
                    padding: '20px', //  boluk
                    border: '1px solid #ccc', // Kenar izgisi
                }}
            >
                <Typography variant="h3" gutterBottom>
                    Ho Geldiniz!
                </Typography>
                <Typography variant="body1" paragraph align='left'>
                    Ben <strong>Etem Oulcan AKPINAR</strong>, bir yazlmc ve teknoloji tutkunu olarak, dijital dnyada yeniliki zmler yaratmaya adanm bir profesyonelim. Kod yazmann tesinde, hayal gcm ve teknik becerilerimi birletirerek, kullanclarn hayatlarn kolaylatracak ve i srelerini hzlandracak projeler zerinde almay seviyorum.
                </Typography>
                <Typography variant="body1" paragraph align='left'>
                    Her satr kodda, sorunlar zmeyi ve kullanclara deer katmay hedefliyorum. Modern teknolojileri ve en iyi uygulamalar takip ederek, web uygulamalar, mobil uygulamalar ve kullanc deneyimini gelitiren projeler gelitiriyorum.
                </Typography>
                <Typography variant="body1" paragraph align='left'>
                    Web sitem, yeteneklerimi, projelerimi ve teknolojiye olan tutkumun bir yansmasdr. Hedefim, mterilerime ve kullanclarma en iyi deneyimi sunmaktr. Fikirlerinizi hayata geirmek ve projelerinizi bir st seviyeye tamak iin buradaym.
                </Typography>
                <Typography variant="body1" paragraph align='left'>
                    Gelin, birlikte yeni ve heyecan verici dijital dnyalar kefedelim!
                </Typography>

                <Stack spacing={2} mt={3}>
                    <Button variant="contained" color="secondary" onClick={() => handleNavigation('/hakkimda')}>
                        Hakkmda
                    </Button>
                    <Button variant="contained" color="secondary" onClick={() => handleNavigation('/hizmetler')}>
                        Hizmetler
                    </Button>
                    <Button variant="contained" color="secondary" onClick={() => handleNavigation('/iletisim')}>
                        letiim
                    </Button>
                </Stack>
            </Box>
        </Container>
    );
};

export default Home;
