import React from 'react';
import Navbar from './Navbar/Navbar'; // Navbar bileenini ie aktarn
import Footer from './Footer/Footer'; // Footer bileenini ie aktarn
import FloatingActionButtons from './FloatingActionButtons/FloatingActionButtons'; // Footer bileenini ie aktarn
import { Container } from '@mui/material';

const Layout = ({ children }) => {
  return (
    <Container style={{ display: 'flex', padding: 0, flexDirection: 'column', minHeight: '100vh', minWidth: '100vw', width: '100vw' }}>
      <Navbar />
      <main style={{ flexGrow: 1 }}>
        {children}
        <FloatingActionButtons />
        </main> {/* Ana ierik alan */}
      <Footer />
    </Container>
  );
};

export default Layout;
